          en: `This year I managed to grow my favorite chili  jalapeo!  It became a really good harvest, and I decided to pickle them in a classic 123 brine (1 part vinegar, 2 parts sugar and 3 parts water). So simple, but so good! Perfect for tacos, hamburgers or just as a little extra kick in the food. `
