  {
    id: 10,
    title: { sv: 'Saffranssnurror i juletid', en: 'Saffron twists for Christmas' },
    excerpt: { sv: 'Idag har jag bakat saffransbullar med extra fyllning  perfekt s hr i juletider.', en: 'Today I baked saffron buns with extra filling  perfect for this time of year.' },
    date: '2025-12-01',
    category: { sv: 'Livsstil', en: 'Lifestyle' },
    imageUrl: '/images/saffransbullar.jpg',
    content: [
      {
        type: 'text',
        content: {
          sv: `Det brjar verkligen knnas som jul nu. Den hr tiden p ret blir kvllarna lite mysigare, ljusen lite varmare och dofterna i kket lite godare  Saffran r en stor favorit hos mig, s i veckan passade jag p att baka saffransbullar. Det r ngot speciellt med att blanda ihop en deg som bde doftar och ser ut som jul 
  
  Jag fljde ett recept frn Arla men gjorde ngra egna justeringar, framfr allt fr att f bullarna extra saftiga. Jag dubblade fyllningen, hoppade ver mandelspnen och formade bullarna som snurror. Resultatet blev otroligt mjuka och smakrika saffranssnurror som gjorde hela kket sdr hrligt juldoftande `,
          en: `It really starts to feel like Christmas now. This time of year, the evenings become a bit cozier, the lights a bit warmer and the scents in the kitchen a bit more delicious  Saffron is a big favorite of mine, so this week I took the opportunity to bake saffron buns. There's something special about mixing together a dough that both smells and looks like Christmas 
  
  I followed a recipe from Arla but made some of my own adjustments, especially to make the buns extra juicy. I doubled the filling, skipped the almond slices and shaped the buns as twists. The result became incredibly soft and flavorful saffron twists that made the whole kitchen wonderfully Christmas-scented `
        }
      },
      {
        type: 'text',
        content: {
          sv: `Ingredienser
  
  Deg:
  50 g jst
  100 g smr
  5 dl mjlk
  2 psar saffran (1 g)
  1 1/2 dl strsocker
  1/2 tsk salt
  ca 17 dl vetemjl
  
  Pensling:
  2 gg,
  prlsocker
  
  Fyllning:
  400 g smr, rumsvarmt
  400 g riven mandelmassa
  2 tsk kardemumma`,
          en: `Ingredients
  
  Dough:
  50 g yeast
  100 g butter
  5 dl milk
  2 bags saffron (1 g)
  1 1/2 dl granulated sugar
  1/2 tsp salt
  about 17 dl wheat flour
  
  Glaze:
  2 eggs,
  pearl sugar
  
  Filling:
  400 g butter, room temperature
  400 g grated almond paste
  2 tsp cardamom`
        }
      },
      {
        type: 'text',
        content: {
          sv: `S hr gjorde jag:
  
  Jag brjade med att smula jsten i bunken till min bakmaskin. Sedan vrmde jag mjlken till 37 grader, blandade ner saffranet i mjlken och hllde den ver jsten. Jag la sedan i smret och tillsatte socker och salt. Efter det arbetade jag in mjlet lite i taget tills degen blev mjuk och smidig. Den fick jsa under bakduk i cirka 45 minuter 
  
  Under tiden rrde jag ihop fyllningen  smr, mandelmassa och kardemumma  tills den blev jmn och krmig  Nr degen jst klart kavlade jag ut den till en stor rektangel och bredde ut fyllningen p halva och vek sedan den andra halvan p. Jag skar degen i remsor, tvinnade dem och snurrade ihop dem (Jag gjorde ven ngra vanliga rullar). Bullarna lade jag direkt p bakpltspapper utan formar. De fick jsa i ytterligare cirka 30 minuter.
  
  Till sist penslade jag snurrorna med uppvispat gg och la p prlsocker och grddade dem i 225 grader i cirka 8 minuter, tills de ftt en vacker gyllene frg 
  
  Det blev riktigt fina saffranssnurror  mjuka, saftiga och fulla av smak. Precis det man vill ha s hr i juletider `,
          en: `Here's how I did it:
  
  I started by crumbling the yeast into the bowl of my bread machine. Then I heated the milk to 37 degrees, mixed in the saffron in the milk and poured it over the yeast. I then added the butter and added sugar and salt. After that, I worked in the flour little by little until the dough became soft and smooth. It was left to rise under a baking cloth for about 45 minutes 
  
  Meanwhile, I mixed together the filling  butter, almond paste and cardamom  until it became smooth and creamy  When the dough had finished rising, I rolled it out into a large rectangle and spread the filling on half and then folded the other half over. I cut the dough into strips, twisted them and rolled them up (I also made some regular rolls). I placed the buns directly on baking paper without forms. They were left to rise for another about 30 minutes.
  
  Finally, I brushed the twists with beaten egg and added pearl sugar and baked them at 225 degrees for about 8 minutes, until they got a beautiful golden color 
  
  They became really nice saffron twists  soft, juicy and full of flavor. Exactly what you want this time of year `
        }
      }
    ]
  },
  {
    id: 11,
    title: { sv: 'En julrimsgenerator byggd med Google Gemini ', en: 'A Christmas rhyme generator built with Google Gemini ' },
    excerpt: { sv: 'Jag har byggt en egen julrimsgenerator med hjlp av Google Gemini  perfekt nr fantasin tryter i juletider.', en: 'I built my own Christmas rhyme generator using Google Gemini  perfect when inspiration runs out during the holidays.' },
    date: '2025-12-12',
    category: { sv: 'Teknik', en: 'Tech' },
    imageUrl: '/images/christmas-rhyme-generator.png',
    content: [
      {
        type: 'text',
        content: {
          sv: `Julen nrmar sig med stormsteg och med den kommer ocks den rliga utmaningen  att hitta p bra julrim. Jag tycker det r mysigt, men ibland str det helt still och d r det sknt med lite hjlp p traven. 
  
  Drfr bestmde jag mig fr att bygga en egen julrimsgenerator, mest som ett roligt litet projekt men ocks fr att faktiskt ha ngot anvndbart infr julklappsinslagningen.`,
          en: `Christmas is approaching at full speed and with it comes the annual challenge  coming up with good Christmas rhymes. I think it's cozy, but sometimes it comes to a complete standstill and then it's nice to have a little help. 
  
  That's why I decided to build my own Christmas rhyme generator, mostly as a fun little project but also to actually have something useful for wrapping Christmas presents.`
        }
      },
      {
        type: 'text',
        content: {
          sv: `Julrimsgeneratorn r byggd med hjlp av Google Gemini och fungerar genom att man helt enkelt beskriver vad presenten r och vilken ton man vill ha  och s genereras ett rim utifrn det. Fokus har varit att hlla det enkelt, snabbt och lttillgngligt, utan krngel.`,
          en: `The Christmas rhyme generator is built using Google Gemini and works by simply describing what the gift is and what tone you want  and then a rhyme is generated based on that. The focus has been to keep it simple, fast and easily accessible, without hassle.`
        }
      },
      {
        type: 'text',
        content: {
          sv: `Det finns redan en fungerande gratisversion uppe, och lnken till generatorn ligger i menyn hr p bloggen fr den som vill testa sjlv. Den r tnkt att vara ett litet hjlpmedel nr fantasin tryter, inte ngot som ska knnas verarbetat eller komplicerat.`,
          en: `There's already a working free version up, and the link to the generator is in the menu here on the blog for anyone who wants to try it themselves. It's meant to be a little helper when inspiration runs out, not something that should feel overworked or complicated.`
        }
      },
      {
        type: 'text',
        content: {
          sv: `Projektet var ocks ett kul stt fr mig att testa Gemini i praktiken och se hur vl det fungerar fr den hr typen av kreativ textgenerering. Det gav mig mjlighet att experimentera bde med prompts och struktur, och samtidigt bygga ngot som faktiskt kommer till anvndning.`,
          en: `The project was also a fun way for me to test Gemini in practice and see how well it works for this type of creative text generation. It gave me the opportunity to experiment with both prompts and structure, while building something that will actually be used.`
        }
      },
      {
        type: 'text',
        content: {
          sv: `Jag har redan ngra ider p hur generatorn kan utvecklas vidare, men just nu knns det mest kul att den r live och gr att anvnda. Perfekt tajming lagom till jul. `,
          en: `I already have some ideas on how the generator can be developed further, but right now it feels most fun that it's live and can be used. Perfect timing just in time for Christmas. `
        }
      }
    ]
  }
